.heading {
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 100vh;
  align-items: center;
}
.heading::before {
  background: var(--heading-background) no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center center;
  opacity: 0.3;
}

.heading__title {
  position: relative;
  font-family: fantasy;
  font-size: 2.5vw;
  top: 35%;
}
